import { useState, useEffect } from 'react';
import {
  ThemeProvider,
  createTheme,
  CssBaseline,
  Container,
  AppBar,
  Toolbar,
  Typography,
  Box,
  Fab,
  Snackbar,
  Alert,
  Chip,
  Stack
} from '@mui/material';
import { Add, Wifi, WifiOff } from '@mui/icons-material';

import { TimerDisplay } from './components/TimerDisplay';
import { TimerCreator } from './components/TimerCreator';
import { TimestampList } from './components/TimestampList';
import { useTimer } from './hooks/useTimer';

/**
 *   
 *      UI 
 */

// Material-UI  
const theme = createTheme({
  palette: {
    primary: {
      main: '#667eea',
    },
    secondary: {
      main: '#f093fb',
    },
    background: {
      default: '#f5f7fa',
    },
  },
  typography: {
    fontFamily: '"Noto Sans KR", "Roboto", "Helvetica", "Arial", sans-serif',
  },
  shape: {
    borderRadius: 12,
  },
});

function App() {
  //  
  const [showCreator, setShowCreator] = useState(true);
  const [userId] = useState(() => `user-${Date.now()}`); //   ID 
  const [currentTimerId, setCurrentTimerId] = useState<string | null>(null);
  const [snackbar, setSnackbar] = useState<{
    open: boolean;
    message: string;
    severity: 'success' | 'error' | 'info' | 'warning';
  }>({
    open: false,
    message: '',
    severity: 'info'
  });
  const [timestampRefreshTrigger, setTimestampRefreshTrigger] = useState(0);

  // URL  ID  (  )
  useEffect(() => {
    const path = window.location.pathname;
    const urlParams = new URLSearchParams(window.location.search);
    
    // /timer/{shareToken}  URL 
    const timerMatch = path.match(/^\/timer\/(.+)$/);
    if (timerMatch) {
      const shareToken = timerMatch[1];
      setCurrentTimerId(shareToken);
      setShowCreator(false);
      return;
    }
    
    //    ( )
    const timerIdFromUrl = urlParams.get('timer');
    if (timerIdFromUrl) {
      setCurrentTimerId(timerIdFromUrl);
      setShowCreator(false);
    }
  }, []);

  //   
  const {
    timer,
    loading,
    error,
    connected,
    createTimer,
    saveTimestamp,
    completeTimer,
    remainingSeconds,
    isCompleted,
    progress
  } = useTimer({
    timerId: currentTimerId || undefined,
    userId,
    autoConnect: true
  });

  /**
   *   
   */
  const showSnackbar = (
    message: string, 
    severity: 'success' | 'error' | 'info' | 'warning' = 'info'
  ) => {
    setSnackbar({ open: true, message, severity });
  };

  /**
   *   
   */
  const handleCreateTimer = async (targetTimeSeconds: number) => {
    try {
      await createTimer(targetTimeSeconds);
      setShowCreator(false);
      showSnackbar(' !', 'success');
      
      // URL  ( )
      if (timer?.shareToken) {
        const token = timer.shareToken.replace('/timer/', '');
        const newUrl = `${window.location.origin}/timer/${token}`;
        window.history.pushState({}, '', newUrl);
      }
    } catch (err) {
      showSnackbar('  .', 'error');
    }
  };

  /**
   *   
   */
  const handleSaveTimestamp = async () => {
    try {
      await saveTimestamp();
      showSnackbar(' !', 'success');
      //    
      setTimestampRefreshTrigger(prev => prev + 1);
    } catch (err) {
      showSnackbar('  .', 'error');
    }
  };

  /**
   *  
   */
  const handleShare = async () => {
    if (!timer?.shareToken) return;
    
    // shareToken /timer/     
    const token = timer.shareToken.replace('/timer/', '');
    const shareUrl = `${window.location.origin}/timer/${token}`;
    
    try {
      //  API 
      await navigator.clipboard.writeText(shareUrl);
      showSnackbar('   !', 'success');
    } catch (err) {
      // :  
      const textArea = document.createElement('textarea');
      textArea.value = shareUrl;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      showSnackbar('  !', 'success');
    }
  };

  /**
   *   
   */
  const handleCompleteTimer = async () => {
    try {
      await completeTimer();
      showSnackbar(' !', 'success');
    } catch (err) {
      showSnackbar('   .', 'error');
    }
  };

  /**
   *   
   */
  const handleNewTimer = () => {
    setShowCreator(true);
    setCurrentTimerId(null);
    window.history.pushState({}, '', window.location.origin);
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      
      {/*  */}
      <AppBar position="static" elevation={0}>
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
             KB EchoTimer
          </Typography>
          
          {/*    */}
          <Stack direction="row" spacing={1} alignItems="center">
            <Chip
              icon={connected ? <Wifi /> : <WifiOff />}
              label={connected ? '' : ' '}
              color={connected ? 'success' : 'error'}
              size="small"
              variant="outlined"
              sx={{ color: 'white', borderColor: 'rgba(255,255,255,0.5)' }}
            />
          </Stack>
        </Toolbar>
      </AppBar>

      {/*   */}
      <Container maxWidth="md" sx={{ py: 4 }}>
        {showCreator ? (
          /*    */
          <TimerCreator
            loading={loading}
            error={error}
            onCreateTimer={handleCreateTimer}
          />
        ) : timer ? (
          /*    */
          <Box>
            <TimerDisplay
              remainingSeconds={remainingSeconds}
              totalSeconds={timer.remainingTime}
              isCompleted={isCompleted}
              progress={progress}
              onlineUserCount={timer.onlineUserCount}
              userRole={timer.userRole}
              timerId={timer.timerId}
              onSave={handleSaveTimestamp}
              onShare={handleShare}
              onComplete={handleCompleteTimer}
            />
            
            {/*   */}
            <Box mt={3} textAlign="center">
              <Typography variant="body2" color="text.secondary" gutterBottom>
                 : {timer.ownerId}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                 : {new Date(timer.targetTime).toLocaleString('ko-KR')}
              </Typography>
            </Box>
            
            {/*   */}
            <TimestampList 
              timerId={timer.timerId} 
              refreshTrigger={timestampRefreshTrigger}
            />
          </Box>
        ) : (
          /*   */
          <Box textAlign="center" py={8}>
            <Typography variant="h6" color="text.secondary">
                ...
            </Typography>
          </Box>
        )}
      </Container>

      {/*    FAB */}
      {!showCreator && (
        <Fab
          color="primary"
          aria-label=" "
          onClick={handleNewTimer}
          sx={{
            position: 'fixed',
            bottom: 24,
            right: 24,
          }}
        >
          <Add />
        </Fab>
      )}

      {/*  () */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert
          onClose={() => setSnackbar({ ...snackbar, open: false })}
          severity={snackbar.severity}
          variant="filled"
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </ThemeProvider>
  );
}

export default App;
